import React, { Component } from 'react';
import './App-header.css';
import PhoneIcon from './header-phone-icon.png';
import Logo from './header-logo.png';
import Hamburger from './hamburger/Hamburger.js';

class AppHeader extends Component {
  render() {
      if(this.props.appHeaderMenuVisibility) {
          return (
              <header className="App-header">
                  <nav className="App-navbar">
                      <div className="App-navbar-phone">
                          <div className="App-navbar-phone__img">
                              <img  src={PhoneIcon} alt="Телефон" />
                          </div>
                          <div className="App-navbar-phone__number">8-<nobr />800-<nobr />234-<nobr />21-<nobr />23</div>
                      </div>
                      <img className="App-navbar-logo" src={Logo} alt="Логотип" />
                      <button className="App-navbar-inquiry Button ButtonWhiteNoBg PreloaderOpasity" href="#">Запрос</button>
                      <Hamburger />
                  </nav>
              </header>
          );
      }
    return (
      <header className="App-header">
        <nav className="App-navbar">
            <img className="App-navbar-logo" src={Logo} alt="Логотип" />
        </nav>
      </header>
    );
  }
}

export default AppHeader;



