import React, { Component } from 'react';
import './App-page-number.css';

class AppPageNumber extends Component {
	
  render() {
    if(this.props.pageNumberThis === 2) {
       return (
        <div className="App-page-number App-page-number-color-black animated fadeInLeft">
          <div className="App-page-number__this">{(this.props.pageNumberThis < 10 ? '0': '')}{this.props.pageNumberThis}</div>
          <div className="App-page-number__slash">/</div>
          <div className="App-page-number__all">{(this.props.pageNumberAll < 10 ? '0': '')}{this.props.pageNumberAll}</div>
        </div>
      );
    } else {
      return (
        <div className="App-page-number App-page-number-color-white animated fadeInLeft">
          <div className="App-page-number__this">{(this.props.pageNumberThis < 10 ? '0': '')}{this.props.pageNumberThis}</div>
          <div className="App-page-number__slash">/</div>
          <div className="App-page-number__all">{(this.props.pageNumberAll < 10 ? '0': '')}{this.props.pageNumberAll}</div>
        </div>
      );
    }
   
  }
}

export default AppPageNumber;


