import React, { Component } from 'react';
import $ from 'jquery';
import './App-page-two.css';
import '../App-pages.css';
import Bitmap from './Bitmap.png';
import LeftArrow from './left-arrow.png';
import RightArrow from './right-arrow.png';

class TabBasic extends Component {

  constructor(props) {
   super(props);
    this.bitmapAnimate = this.bitmapAnimate.bind(this);
  }

  bitmapAnimate() {
    $('#Tab-basic-top__bitmap').addClass('bitmapAnimate')
  }

  render() {
    return (
      <div className="Tab-basic animated fadeInRight" onMouseOver={this.bitmapAnimate}>
        <div className="Tab-basic-top">
          <h4 className="Tab-basic-top__item">Экспресс авиа <br />доставка из Китая</h4>
          <p className="Tab-basic-top__item">Перевозка сборных грузов из Китая в составе сборного контейнера оптимально подходит для перевозки небольших партий в разумные сроки по низкой цене.</p>
          <img className="Tab-basic-top__item" id="Tab-basic-top__bitmap" src={Bitmap} alt="Самолет" />
          <div className="Tab-basic-top__item">
            <div className="Tab-basic-top-arrow-left"><img src={LeftArrow} alt="Налево" /></div>
            <div className="Tab-basic-top-arrow-right"><img src={RightArrow} alt="Направо" /></div>
          </div>
        </div>
        <div className="Tab-basic-bottom">
          <div className="Tab-basic-bottom__item">
            <button className="Button ButtonBlack" href="#">Подробнее</button>
          </div>
          <p className="Tab-basic-bottom__item">Минимальная партия груза — <span>400 кг.</span></p>
          <p className="Tab-basic-bottom__item">Срок доставки — <span>45-50 дней</span></p>
          <p className="Tab-basic-bottom__item">Стоимость доставки — <span>0,9$/кг.</span></p>
          <div className="Tab-basic-bottom__item">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </div>

      );
  }
};

class TabAdditional extends Component {
  render() {
    return (
      <div className="Tab-additional animated fadeInLeft">
        <div className="Tab-additional__item">
          <h6>Таможенные <br />оформления грузов <br />из Китая</h6>
          <p>Полный комплекс услуг по таможенной очистке товаров из Китая: широкие возможности с Pacific Asia Group.</p>
        </div>
        <div className="Tab-additional__item">
          <h6>Финансовое <br />сопровождение</h6>
          <p>Вам необходимо оплатить товар? - предложим различные варианты перевода денег в Китай для юридических лиц.</p>
        </div>
        <div className="Tab-additional__item">
          <h6>Поиск <br />производителей <br />в Китае</h6>
          <p>Помощь в поиске надежного производителя абсолютно бесплатно. Вы получите прямые контакты заводов из базы производителей, проверенных нами на реальных поставках, либо мы осуществим новый поиск.</p>
        </div>
        <div className="Tab-additional__item">
          <h6>Эскпорт <br />в Китай</h6>
          <p>Вы получите всю необходимую информацию для выхода на новый рынок и организации успешного экспорта в Китай из России.</p>
        </div>
        <div className="Tab-additional__item">
          <h6>Оборудование <br />из Китая</h6>
          <p>Недорогие станки для малого бизнеса или целые автоматизированные линии – вы можете выгодно купить оборудование в Китае и получить его на своем складе в Москве или любом другом городе РФ.</p>
        </div>
        <div className="Tab-additional__item">
          <h6>Импорт <br />из Китая</h6>
          <p>Транспортная компания Pacific Asia Group предлагает услуги организации экспорта и импорта из Китая.</p>
        </div>
      </div>
    );
  }
};

class AppPageTwo extends Component {
  constructor(props) {
      super(props);
      this.state = {
        tabContent: <TabBasic />
      };
      this.tabBasic = this.tabBasic.bind(this);
      this.tabAdditional = this.tabAdditional.bind(this);
    }

    tabBasic() {
      this.setState({
        tabContent: <TabBasic />
      });
      $('.App-page-two-top-tabs').removeClass('App-page-two-top-tabs-additional');
      $('.App-page-two-top-tabs').addClass('App-page-two-top-tabs-basic');
      setTimeout(function() {
        $('#Tab-basic-top__bitmap').addClass('bitmapAnimate');
      }, 300);
      
    }
    tabAdditional() {
      this.setState({
        tabContent: <TabAdditional />
      });
      $('.App-page-two-top-tabs').removeClass('App-page-two-top-tabs-basic');
      $('.App-page-two-top-tabs').addClass('App-page-two-top-tabs-additional');
    }

  render() {
    return (
      <div className="App-page App-page-two">
      	<div className="App-page-two__top">
      		<h2>Транспортные <br />услуги</h2>
          <div className="App-page-two-top-tabs App-page-two-top-tabs-basic">
            <div className="App-page-two-top-tabs__button" onClick={this.tabBasic}>Основные</div>
            <div className="App-page-two-top-tabs__button" onClick={this.tabAdditional}>Дополнительные</div>
          </div>
      	</div>
        <div className="App-page-two__bottom">
          {this.state.tabContent}
        </div>
      </div>
    );
  }
}

export default AppPageTwo;


