import React, { Component } from 'react';
import './App-pages.css';
import 'dragscroll/dragscroll.js';
import AppPageNumber from './app-page-number/App-page-number.js';
import AppPageOne from './app-page-one/App-page-one.js';
import AppPageTwo from './app-page-two/App-page-two.js';
import AppPageThree from './app-page-three/App-page-three.js';


class AppPages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNumberThis: 1
    };
    this.pageNumberScroll = this.pageNumberScroll.bind(this);
  }
  pageNumberScroll() {
    const appPagesScroll = document.querySelector('.App-pages').scrollTop;
    const appPages = document.querySelectorAll('.App-page');

    for(var i = 0; i < appPages.length-1; i++) {
      if(appPagesScroll >= appPages[i].offsetTop - 150 && appPagesScroll < appPages[i+1].offsetTop - 150) {
        this.setState({
          pageNumberThis: i + 1
        });
        break;
      }  
    }
    if (appPagesScroll > appPages[appPages.length - 1].offsetTop - 150) {
      this.setState({
          pageNumberThis: appPages.length
        });
    }
  }

  render() {
    if(this.props.appPagesVisibility) {
        return (
            <div className="App-pages dragscroll" onScroll={this.pageNumberScroll}>
                <AppPageNumber pageNumberThis = {this.state.pageNumberThis} pageNumberAll="3" />
                <AppPageOne play={this.props.play} pause={this.props.pause} playerOpen={this.props.playerOpen} />
                <AppPageTwo />
                <AppPageThree />
            </div>
        );
    }

    return (
        <div className="App-pages dragscroll" onScroll={this.pageNumberScroll}>

        </div>
      );

  }
}

export default AppPages;


