import React, { Component } from 'react';
import $ from "jquery";
import './App-footer.css';
import DownArrow from './App-footer-down-arrow.png';
import Twitter from './Twitter.png';
import Facebook from './Facebook.png';

class AppFooter extends Component {
	constructor(props) {
    super(props);
    this.leafDown = this.leafDown.bind(this);
  }
	leafDown() {
		const appPagesScroll = document.querySelector('.App-pages').scrollTop;
    const appPages = document.querySelectorAll('.App-page');

    for(var i = 0; i < appPages.length-1; i++) {
    	if(appPagesScroll >= appPages[i].offsetTop && appPagesScroll < appPages[i+1].offsetTop) {
    		$('.App-pages').animate({scrollTop: appPages[i+1].offsetTop}, 700);
    		break;
    	}  
    }
	}
  render() {
    return (
      <footer className="App-footer PreloaderOpasity" onClick={this.leafDown}>
      	<div className="App-footer__content">
      		<div className="App-footer__content__text">Листайте вниз</div>
      		<img className="App-footer__content__down-arrow" src={DownArrow} alt="Стрелка вниз" />
      	</div>
      	<div className="App-footer__contacts">
      		<img className="App-footer__contacts__facebook" src={Facebook} alt="FaceBook" />
      		<img className="App-footer__contacts__twitter" src={Twitter} alt="Twitter" />
      	</div>
      </footer>
    );
  }
}

export default AppFooter;


