import React, { Component } from 'react';
import $ from 'jquery';

import "video-react/dist/video-react.css";
import { Player } from 'video-react';

import './App.css';
import AppHeader from './app-header/App-header.js';
import AppFooter from './app-footer/App-footer.js';
import AppPages from './app-pages/App-pages.js';



class App extends Component {
	constructor(props) {
    super(props);

    this.state = {
        appHeaderVisibility: true,
        appHeaderMenuVisibility: false,
        appPagesVisibility: false,
        appFooterVisibility: false,
        playerVisibility: false,
        playerOpen: false
    };

    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.playerOpen = this.playerOpen.bind(this);
    this.playerClose = this.playerClose.bind(this);
  }

    componentDidMount() {
        setTimeout(() => {
            this.setState({
                appHeaderMenuVisibility: true,
                appPagesVisibility: true,
                appFooterVisibility: true,
                playerVisibility: true
            })
        }, 1000);
    }

  play() {
	    $('.App-player').css('display', 'block');
	    this.refs.player.play();
  }

  pause() {
      $('.App-player').css('display', 'none');
      this.refs.player.pause();
  }

  playerOpen() {
  	this.setState({
        appHeaderVisibility: false,
        appPagesVisibility: false,
        appFooterVisibility: false,
        playerOpen: true
    });
  }

  playerClose() {
  	this.setState({
        appHeaderVisibility: true,
        appPagesVisibility: true,
        appFooterVisibility: true,
        playerOpen: false
    });
    this.pause()
  }

  render() {

	    var player = <div className="App-player">
            {this.state.playerOpen && <span className="close" onClick={this.playerClose}></span> }
            {!this.state.playerOpen && <div className="App-player-blur"></div>}
            <Player playsInline ref="player" src="http://media.w3.org/2010/05/bunny/movie.mp4" />
        </div>

      var preloader = <div className="App-preloader"></div>


    return (
      <div className="App">
          {this.state.playerVisibility && player}
          {this.state.appHeaderVisibility && <AppHeader appHeaderMenuVisibility = {this.state.appHeaderMenuVisibility}  /> }
          { preloader }
          <AppPages appPagesVisibility = { this.state.appPagesVisibility } play={this.play} pause={this.pause} playerOpen={this.playerOpen} />
          {this.state.appFooterVisibility && <AppFooter /> }
      </div>
    );
  }
}

export default App;



