import React, { Component } from 'react';

import './App-page-one.css';
import Play from './app-page-one-play.png';

class AppPageOne extends Component {
  render() {
    return (
      <div className="App-page App-page-one animated fadeInLeft">
        <div className="App-page__content App-page-one__content">
          <h1 className="App-page-one__item">Из Китая<br /> в Россию <br /><span>доставка <br />любых грузов <br />под ключ</span></h1>
          <p className="App-page-one__item">от 45 кг до целого <br />контейнера</p>
          <div className="App-page-one__item">
            <button className="Button ButtonWhite" href="#">Оставить заявку</button>
          </div>
          <p className="App-page-one__item">Компания PACIFIC ASIA GROUP с 2007 года предоставляет качественный сервис по доставке грузов из Китая.</p>
          <div className="App-page-one__item">
            <div className="App-page-one__item__play-video" onMouseEnter={this.props.play} onMouseLeave={this.props.pause} onClick={this.props.playerOpen}>
              <div className="App-page-one__item__img">
                <img src={Play} alt="Play" />
              </div>
              <div className="App-page-one__item__text">Смотреть видео</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AppPageOne;


