import React, { Component } from 'react';
import './App-page-three.css';
import '../App-pages.css'


class AppPageThree extends Component {
  render() {
    return (
      <div className="App-page App-page-three">
        <div className="App-page__content App-page-three__content">
            <h2 className="App-page-three__title">Наши <br />преимущества</h2>
            <div className="App-page-three__list">
            	<div className="App-page-three__list__item">
            		<h3>A</h3>
            		<h6>Мы привезем <br />дешевле</h6>
            		<p>Отправте нам Ваш упоковочный лист и мы сделаем оптимальный расчет доставки Вашего груза в Киев.</p>
            	</div>
            	<div className="App-page-three__list__item">
            		<h3>B</h3>
            		<h6>Полный спектр <br />услуг</h6>
            		<p>Все что Вам нужно для успешной работы с Китаем в одном месте.</p>
            	</div>
            	<div className="App-page-three__list__item">
            		<h3>C</h3>
            		<h6>Фиксированная цена <br />и сроки поставки</h6>
            		<p>После утверждения цены стоимость перевозки остается неизменной. При повышении сроков поставки выплачиваем неустойку.</p>
            	</div>
            	<div className="App-page-three__list__item">
            		<h3>D</h3>
            		<h6>Каждый груз <br />страхуется</h6>
            		<p>Мы несем полную материальную ответственность за сохранность Вашего груза во время перевозки из Китая.</p>
            	</div>
            	<div className="App-page-three__list__item">
            		<h3>E</h3>
            		<h6>Наличие сотрудников <br />и складов в Китае</h6>
            		<p>Для оперативной работы и консолидации Вашего груза.</p>
            	</div>
            </div>
        </div>
      </div>
    );
  }
}

export default AppPageThree;


